@import url(https://fonts.googleapis.com/css?family=DM+Sans:400,500,700&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'DM Sans', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: #f3f6fd;
}

.card {
	display: flex;
	align-items: center;
	flex-direction: column;
	background-color: #fff;
	border-radius: 10px;
	padding: 15px;
	transition: all 0.2s ease-out;
	box-shadow: 2px 2px 10px 0px rgb(0 0 0 / 15%);
}

.card:hover {
	box-shadow: 2px 2px 10px 1px rgb(0 0 0 / 25%);
}

.card img {
	height: 8rem;
	width: 8rem;
	border-radius: 4rem;
}

.card h3 {
	margin-bottom: 0.5rem;
}

.card address,
.card span {
	font-size: 0.9rem;
}

.card-list {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
	grid-gap: 24px;
	align-items: stretch;
}

.search-content {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 2rem 0;
}

.search-content .search {
	-webkit-appearance: none;
	border: none;
	outline: none;
	padding: 10px;
	font-size: 1rem;
	width: 300px;
	border-radius: 5px;
	box-shadow: 0px 2px 10px 1px rgb(0 0 0 / 15%);
	font-family: 'DM Sans', sans-serif;
}

.App {
	width: 95vw;
	margin: 0 auto;
}

